.load{
    justify-content: center;
    text-align: center;
    position: 0 0;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 30px;
    padding-right: 30px;
    background-color: red;
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    border: 1px solid #f2e9e4;
    cursor: pointer;
    width: 260px;
    border-radius: 40px;
    height: 60px;
    line-height: 38px;
    font-size: 1rem;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.load:hover{
    -moz-transition-duration: .3s;
    -webkit-transition-delay: .3s;
            transition-delay: .3s;
   
    background-color: transparent;
    color: #f2e9e4;
}
